import React from 'react';
import UserHeader from '../components/UserHeader';
import CourseCard from '../components/CourseCard';
import Button from '../components/Button';
import PostCard from '../components/PostCard';
import Footer from '../components/Footer';
import '../css/AboutCourse.css';

class AboutCourse extends React.Component {
  constructor(props) {
    super(props);

    this.cardsLeft = [];
    this.cardsRight = [];
    cardsLeft.forEach(card => this.cardsLeft.push(<CourseCard {...card} />));
    cardsRight.forEach(card => this.cardsRight.push(<CourseCard {...card} />));
  }

  render () {
    return (
      <div className="aboutCourse">
        <UserHeader />
        <section className="intro">
          <h1>Cincia da Computao</h1>
          <img src={require('../imgs/computerScience.jpg')} alt="course"/>
        </section>
        <section>
          <div className="cards">
            <div className="left">
              {this.cardsLeft}
            </div>
            <div className="right">
              {this.cardsRight}
            </div>
          </div>

          <Button text="Voltar para Conhecendo Profisses" href="/vocare/discover"/>
        </section>
        <Footer fill />
      </div>
    );
  }
}

const cardsLeft = [
  {
    title: 'Sobre o curso',
    text: [
      `O bacharel da Cincia da Computao trabalha, fundamentalmente, na 
      elaborao de programas de informtica, para computadores ou 
      dispositivos mveis, como celulares e tablets.`,
      `Analisa as necessidades dos usurios, desenvolve softwares e 
      aplicativos, gerencia equipes de criao e instala sistemas de 
      computao. Cria ferramentas de informtica, dos softwares mais 
      bsicos, como os usados para controle de estoques, at os mais complexos 
      sistemas de processamento de informaes.`,
      `Tambm d assistncia aos usurios, manuteno a redes de computadores 
      e a conexes com a internet. Pode, ainda, implantar e gerenciar bancos 
      de dados e instalar sistemas de segurana para as operaes de compra e 
      venda pela rede.`,
      <Button text="Fonte" href="https://guiadoestudante.abril.com.br/profissoes/ciencia-da-computacao/" fill newTab/>
    ]
  }, {
    title: 'Qual  o salrio?',
    text: [
      `Todos os salrios so mdias mensais, podendo variar para baixo ou para cima`,
      <div>
        <b>Ganho no incio</b>
        <ul>
          <li>Em pequenas ou mdias empresas: de R$ 2 mil a R$ 4 mil.</li>
          <li>Em grandes empresas: de R$ 2 mil a R$ 4,5 mil. </li>
        </ul>
      </div>,
      <div>
        <b>Ganho escalo intermedirio </b>
        <ul>
          <li>Em pequenas ou mdias empresas: de R$ 5 mil a R$ 9 mil. </li>
          <li>Em grandes empresas: de R$ 6 mil a R$ 11 mil.</li>
        </ul>
      </div>,
      <div>
        <b>Ganho no auge</b>
        <ul>
          <li>Em pequenas ou mdias empresas: de R$ 9 mil a R$ 14 mil.</li>
          <li>Em grandes empresas: de R$ 10 mil a R$ 15 mil. </li>
        </ul>
      </div>,
      <Button text="Fonte" href="https://ultimosegundo.ig.com.br/educacao/guia-de-profissoes/ciencia-da-computacao/4edd0d5a51881c5a34000001.html" fill newTab/>
    ],
  }
];

const cardsRight = [
  {
    title: 'Com o que posso trabalhar?',
    text: [
      `Existem diversos setores possveis para trabalho dentro da cincia da
      computao, alguns deles so:`,
      <ul>
        <li><b>Banco de Dados</b></li>
        <li><b>Desenvolvimento de Sistemas</b></li>
        <li><b>Desenvolvimento mobile</b></li>
        <li><b>Manuteno de redes</b></li>
        <li><b>Marketing</b></li>
      </ul>
    ]
  }, {
    title: 'Veja o que esto discutindo!',
    text: 
      <PostCard
        title='O que  melhor? Cincia da Computao ou Engenharia?'
        message='Lorem lorem lorem  ipsum dolor sit amet, lorem lorem lorem'
        image='bart.jpg'
        link='/vocare/post'
      />
  }, {
    title: 'Quer saber mais sobre?',
    text: 'Abaixo temos um vdeo do Guia de Profisses sobre Cincia da Computao!',
    video: 'https://www.youtube.com/embed/e0mqSm6XKrI'
  }
]

export default AboutCourse;


