import React from 'react';
import UserHeader from '../components/UserHeader';
import Message from '../components/Message';
import Button from '../components/Button';
import Footer from '../components/Footer';
import '../css/Post.css';

function Post() {
  let answers = [];
  post.answers.forEach(answer => answers.push(<Message {...answer} />))

  return (
    <div className="post">
      <UserHeader />
      <section>
        <div className="messages">
          <Message {...post.question}>
            <Button text="Responder"/>
          </Message>
          <h1>Respostas</h1>
          <div className="answers">
            {answers}
          </div>
        </div>
        <div className="optionsColumn">
          <Button text="Voltar para a discusso" href="/vocare/discussion" />
          <div></div>
          <div></div>
        </div>
      </section>
      <Footer fill />
    </div>
  );
}

const users = {
  bart: {
    name: 'Vitor Bartier',
    level: 3,
    messages: 666,
    image: 'bart.jpg'
  },

  igor: {
    name: 'Igor Mandello',
    level: 3,
    messages: 333,
    image: 'igor.jpg'
  },

  hideki: {
    name: 'Lucas Hideki',
    level: 4,
    messages: 757,
    image: 'hideki.jpg'
  },
};

const post = {
  question: {
    title: 'Eu tenho uma dvida: o que  melhor entre cincia da computao e engenharia da computao?',
    date: '04/09/2018',
    time: '19h02',
    message: `Ento gente, acho que bastantes usurios tem essa mesma dvida ento estou lanando aqui
      (claro que para me ajudar tambm ), quais so as diferenas entre os cursos, qual  melhor,
      qual eu deveria cursar? Se algum conseguir ajudar ficaria bem feliz!! `,
    user: users.bart
  },
  answers: [
    {
      date: '04/09/2018',
      time: '19h32',
      message: `Olha cara, eu to com a mesma dvida que voc, e sinceramente, eu
        estou indo mais para o lado da cincia, estou fazendo isso porque a carga
        horria, alm de ser menor,  mais vivel para mim, pois fiz tcnico e 
        gostaria de estagiar tambm... Outro motivo tambm  o currculo, j que
        em engenharia, acabamos tendo matrias que eu no gostaria de fazer, como
        fsica e qumica.`,
      user: users.igor
    }, {
      date: '04/09/2018',
      time: '20h17',
      message: `Oi galera! Estou cursando o bacharel em cincia da computao e estou
        gostando bastante da deciso que fiz. Quando precisei acatar qual curso eu iria
        escolher, fui bastante pelo ponto do Igor, por isso estou fazendo esse. Mas na
        faculdade tenho amigos que fazem engenharia tambm, e eles dizem que no  to ruim,
        apenas mais puxado.`,
      user: users.hideki
    }, {
      date: '05/09/2018',
      time: '12h45',
      message: `Obrigado gente ! As respostas ajudaram bastante, agora vou dar uma pensada
        melhor e ver se eu chego em uma concluso, qualquer coisa, vou reabrir essa discusso.`,
      user: users.bart
    }
  ]
}

export default Post;


