import React from 'react';
import Header from '../components/Header';
import Feature from '../components/Feature';
import Button from '../components/Button';
import Footer from '../components/Footer';
import '../css/Home.css';

function Home() {
  return (
    <div className="home">
      <Header />
      <section className="intro">
        <h1>VOcAre</h1>
        <blockquote>
          <p>"No haver borboletas se a vida no passar por longas e silenciosas metamorfoses."</p>
          <cite>Rubem Alves</cite>
        </blockquote>
      </section>
      <section className="features">
        <h2>Por que Vocare?</h2>
        <div>
          <Feature {...features.autoconhecimento} />
          <Feature {...features.ambienteDeDiscussao} />
          <Feature {...features.conhecimentoDeProfissoes} />
        </div>

        <span>Saiba mais sobre nossos</span>
        <Button text="Diferenciais" href="/vocare/about" />
      </section>
      <section className="problem">
        <h2>Problema</h2>
        <div className="data">
          <img className="shadow" src={require("../imgs/exclamation.svg")} alt="exclamation"/>
          <div>
            <p>
              <span>75%</span> 
              dos alunos do COTUCA no sabem qual carreira seguir.
            </p>
            <p>
              <span>50%</span> 
              dos alunos do ensino mdio no sabem qual profisso escolher.
            </p>
          </div>
        </div>
        <div className="explanation">
          <div>
            <h3>Por que  preocupante?</h3>
            <p>
              Trabalhar com o que voc no gosta  chato e tedioso. Queremos
              jovens que se <b>reconheam com o seu trabalho</b>, pois 
              entendemos que uma sociedade saudvel  aquela em que as pessoas 
              se sintam bem com o que fazem
            </p>
          </div>
          <div>
            <h3>Como resolver esse problema?</h3>
            <p>
              Com a plataforma Vocare voc poder <b>explorar, discutir e aprender </b>
              com outras pessoas sobre diferentes profisses em um ambiente totalmente
              interativo. Tudo isso para voc escolher sua carreira com segurana!
            </p>
          </div>
        </div>

        <span>Saiba mais sobre o</span>
        <Button text="Problema" href="/vocare/about" />
      </section>
      <section className="price">
        <h2>E qual o preo disso?</h2>

        <span>Tudo isso de graa e para sempre!</span>
        <Button text="Cadastre-se" href="/vocare/signup" fill />
      </section>
      <Footer />
    </div>
  );
}

const features = {
  autoconhecimento: {
    icon: 'confident.svg',
    title: 'Autoconhecimento',
    description: 'Realize testes e descubra quais profisses tem a ver com seu estilo',
  },

  ambienteDeDiscussao: {
    icon: 'discussion.svg',
    title: 'Ambiente de Discusso',
    description: 'Descubra como  o dia a dia daquela profisso, quais os desafios da carreira e muito mais!'
  },

  conhecimentoDeProfissoes: {
    icon: 'sign.svg',
    title: 'Conhecimento de Profisses',
    description: 'Ainda em dvida? Veja com o mximo de detalhes sobre determinados cursos e profisses. Ns te guiamos.'
  }
};

export default Home;


