import React from 'react';
import UserHeader from '../components/UserHeader';
import ComboBox from '../components/ComboBox';
import PostCard from '../components/PostCard';
import Button from '../components/Button';
import Footer from '../components/Footer';
import '../css/Discussion.css';

function Discussion() {
  let postsElements = [];
  posts.forEach(post => postsElements.push(<PostCard {...post}/>));

  return (
    <div className="discussion">
      <UserHeader />
      <section className="info">
        <h1>Discusso</h1>
        <p>Aqui voc pode discutir com outros usurios e finalmente decidir o que voc mais gosta de fazer!</p>
      </section>
      <section className="filters">
        <ComboBox label="Filtrar por:" options={['reas de interesse', 'Exatas', 'Humanas', 'Biolgicas']}/>
        <ComboBox label="Ordenar por:" options={['Mais recente', 'Mais antigo']}/>
      </section>
      <section className="posts">
        <div className="postCards">
          {postsElements}
          <span></span>
        </div>
        <div className="optionsColumn">
          <Button text="Criar novo post"/>
          <div className="card"></div>
          <div className="card"></div>
        </div>
      </section>
      <Footer fill />
    </div>
  );
}

const posts = [
  {
    title: 'Eu tenho uma dvida: o que  melhor entre cincia da computao e engenharia da computao?',
    message: `Ento gente, acho que bastantes usurios tem essa mesma dvida ento estou lanando aqui
      (claro que para me ajudar tambm ), quais so as diferenas entre os cursos, qual  melhor,
      qual eu...`,
    image: 'bart.jpg',
    link: '/vocare/post'
  }, {
    title: 'Anlise e Desenvolvimento de Sistemas vale a pena?',
    message: `Galera, tava bem em dvida entre ela, cincia da computao e engenharia da computao,
      hoje em dia ainda vale a pena fazer esse curso? Se sim, por qu? Obrigado.`,
    image: 'igor.jpg',
    link: '/vocare/post'
  }, {
    title: 'Engenharia da Computao  muito corrido?',
    message: `Eu estou terminando meu tcnico e provavelmente vou estagiar, ser que se eu escolher
      cursar Engenharia, eu vou acabar no dando conta do trabalho?`,
    image: 'valente.jpg',
    link: '/vocare/post'
  }
]

export default Discussion;


