import React from 'react';
import UserHeader from '../components/UserHeader';
import Button from '../components/Button';
import Footer from '../components/Footer';
import '../css/Dashboard.css';

function Dashboard() {
  return (
    <div className="dashboard">
      <UserHeader />
      <section className="userData">
        <h1>Bem vindo, Igor</h1>
        <div>
          <span>Seu nvel atual: 3</span>
          <div className="level">
            <span></span>
            <div className="active"></div>
            <div className="active"></div>
            <div className="active"></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </section>
      <section className="objectives">
        <div>
          <div>
            <h2>Objetivo atual</h2>
            <p>
              Voc est em dvida entre algumas profisses, seu objetivo agora
               decidir entre essas qual voc quer que seja sua rea profissional, 
              essa  uma deciso bem importante e difcil, ento tome seu tempo e 
              no se apresse, para que faa a melhor deciso possvel.
            </p>
            <p>
              Voc pode saber mais sobre cada rea que est em dvida acessando as 
              pginas respectivas a elas:
            </p>
          </div>

          <div className="courses">
            <Button text="Engenharia da Computao" />
            <Button text="Cincia da Computao" href="/vocare/aboutCourse" />
            <Button text="Anlise e Desenvolvimento de Sistemas" />
          </div>

          <div>
            <p>
              Tambm  possvel debater com os outros usurios do site para decidir 
              qual a profisso que mais combina com voc em nosso espao de discusso.
            </p>
            <Button text="Acessar Frum" href="/vocare/discussion" />
          </div>
        </div>
        <div>
          <h2>O que esto debatendo...</h2>
          <div className="post card">
            <div>
              <div>
                <img src={require('../components/assets/bart.jpg')} alt="user" className="card"/>
              </div>
              <div>
                <h3>Eu tenho uma dvida: o que  melhor entre cincia da computao e engenharia da computao?</h3>
                <p>
                  Ento gente, acho que bastantes usurios tem essa mesma dvida ento estou lanando aqui...
                </p>
              </div>
            </div>
            <Button text="Acesse essa discusso" href="/vocare/post" fill />
          </div>
          <Button text="Veja outras discusses" href="/vocare/discussion" />
        </div>
      </section>
      <Footer fill />
    </div>
  );
}

export default Dashboard;


